import { useState, useEffect } from "react"
import Aria2Client from "./Aria2Client"


export function useWindowSize() {
  const [size, set] = useState({
    width: window.innerWidth,
    height: window.innerHeight
  })

  useEffect(() => {
    function onResize(e: any) {
      set({
        width: window.innerWidth,
        height: window.innerHeight
      })
    }

    window.addEventListener('resize', onResize)

    return () => window.removeEventListener('resize', onResize)
  }, [])

  return { width: size.width, height: size.height }
}

export function useTask(aria2: Aria2Client, interval: number) {
  const [list1, set1] = useState<any>([])
  const [list2, set2] = useState<any>([])

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2) {
        aria2.tellActive()
          .then((val: any) => { set1(val) })
        aria2.tellWaiting(0, 100)
          .then((val: any) => { set2(val) })
      }
    }, interval)
    return () => clearInterval(t)
  }, [aria2, interval])

  return [...list1, ...list2]
}

export function useComplete(aria2: Aria2Client, interval: number) {
  const [list, set] = useState<any>([])

  useEffect(() => {
    set([])
  }, [aria2])

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2) {
        aria2.tellStopped(0, 100).then((list: any) => {
          set(list)
        }, () => {set([])})
      }
    }, interval)
    return () => clearInterval(t)
  }, [aria2, interval])

  return list
}

export function useStatus(aria2: Aria2Client, gid: string) {
  const [info, set] = useState({})

  useEffect(() => {
    set({})
  }, [aria2])

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2 && gid) {
        aria2.tellStatus(gid).then((info: any) => {
          set(info)
        }, () => {set({})})
      } else set({})
    }, 1000)

    return () => clearInterval(t)
  }, [aria2, gid])

  return info
}

export function useGlobalStat(aria2: Aria2Client, interval: number) {
  const [stat, set] = useState({})

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2) {
        aria2.getGlobalStat().then((val:any) => {
          set(val)
        })
      }
    }, interval)
    return () => clearInterval(t)
  }, [aria2, interval])

  return stat
}

export function useGlobalOption(aria2: Aria2Client) {
  const [globalOption, set] = useState({})

  useEffect(() => {
    if (aria2) {
      aria2.getGlobalOption().then((val: any) => {
        set(val)
      }, () => {set({})})
    }
  }, [aria2])

  return { globalOption, updateGlobalOption: () => {set({...globalOption})}}
}

