import './App.css'

import React, { useEffect, useState } from 'react'
import { Tabs, Select, message, } from 'antd'

import { useWindowSize, useGlobalOption, useGlobalStat } from './hooks'
import { TaskList, Complete, Setting } from './components'
import Aria2Client from './Aria2Client'
import _fileSize from './_filesize'


import {
  SettingOutlined,
  DownloadOutlined,
  CheckCircleOutlined,
} from '@ant-design/icons'


export default function App() {

  const [__serverList, set__serverList] = useState(
    JSON.parse(localStorage.getItem('aria2-server-list') || '[]')
  )

  const [clinetState, setClientState] = useState(0)

  function onDropdown(open) {
    if (open && !__serverList.length) {
      message.info('   ')
      setSelectServer({})
    }
  }
  function selectServerChange(key) {
    const find = __serverList.find(item => item.name === key)
    setSelectServer( find )
  }

  const [selectServer, setSelectServer] = useState({})
  const serverSelect = (
    <Select placeholder=''
      dropdownMatchSelectWidth allowClear
      onChange={selectServerChange}
      onClear={() => {
        message.info('')
        setClientState(0)
      }}
      onDropdownVisibleChange={onDropdown}
    >
      {
        __serverList.map((item, idx) => {
          return (
            <Select.Option value={item.name} key={idx}>
              {item.name}
            </Select.Option>
          )
        })
      }
    </Select>
  )

  useEffect(() => { //  localStorage
    localStorage.setItem(
      'aria2-server-list',
      JSON.stringify(__serverList)
    )
  }, [__serverList])

  const [aria2Client, setAria2Client] = useState(null)
  useEffect(() => { //  aria2Client
    if ( selectServer && selectServer.ip && selectServer.port ) {
      const aria2 = new Aria2Client(
        selectServer.ip,
        selectServer.port,
        selectServer.secret
      )
      setAria2Client(aria2)
    } else setAria2Client(null)
  }, [selectServer])

  useEffect(() => { // 
    if (aria2Client) {
      message.loading({
        content: '',
        key: 'clinetState',
      })
      aria2Client.ready()
        .then(() => {
          setTimeout(() => {
            message.success({
              content: '',
              key: 'clinetState',
            })
          })
          setClientState(1)
        })
        .catch(() => {
          setTimeout(() => {
            message.error({
              content: '',
              key: 'clinetState',
            })
          })
          setClientState(0)
        })
      }
    window.aria2Client = aria2Client
  }, [aria2Client])

  /**
   * @example
   * {
        downloadSpeed: "5459",
        numActive: "2",
        numStopped: "0",
        numStoppedTotal: "0",
        numWaiting: "0",
        uploadSpeed: "0",
      }
   */
  const globalStat = useGlobalStat(aria2Client, 1000)
  useEffect(() => {
    if (globalStat.numActive && globalStat.numActive > 0) {
      document.title = `${_fileSize(globalStat.downloadSpeed)}/s`
    } else document.title = 'Aria2 UI'
  }, [globalStat])

  const { globalOption, updateGlobalOption } = useGlobalOption(aria2Client)

  const tabs = [
    {
      tab: <span><DownloadOutlined /></span>,
      content: <TaskList
        aria2Client={aria2Client}
        clinetState={clinetState} />
    }, {
      tab: <span><CheckCircleOutlined /></span>,
      content: <Complete
        aria2Client={aria2Client}
        clinetState={clinetState} />
    }, {
      tab: <span><SettingOutlined /></span>,
      content: <Setting aria2Client={aria2Client}
        stateServerList={[__serverList, set__serverList]}
        clinetState={clinetState}
        globalOption={globalOption} />
    },
  ]

  // 
  const [__viewMode, set__viewMode] = useState('')
  const { width } = useWindowSize()
  useEffect(() => {
    if (width > 900) set__viewMode('box')
    else set__viewMode('full-screen')
  }, [width])


  return (
    <div className={`App ${__viewMode}`}>
      <Tabs tabPosition='left'
        tabBarExtraContent={{ left: serverSelect }}
        onChange={updateGlobalOption} //  tab 
      >
        {
          tabs.map((item, idx) => {
            return (
              <Tabs.TabPane tab={item.tab} key={idx}>
                {item.content}
              </Tabs.TabPane>
            )
          })
        }
      </Tabs>
    </div>
  )
}
