export default class Aria2Client {
  id          : number
  ws          : WebSocket | null
  readyPromise: Promise<Aria2Client>
  callbacks   : { // id
    [id: number]: (data: any) => void
  } = {} // 
  constructor(
    public ip    : string,
    public port  : string | Number,
    public secret: string | Number
  ) {
    this.id = 1 // if (data.id)

    try {
      this.ws = new WebSocket(`ws://${ip}:${port}/jsonrpc`)
    } catch (e) {
      this.ws = null
    }

    this.readyPromise = new Promise((resolve, reject) => {
      this.ws?.addEventListener('open', () => {
        resolve(this)
      })

      this.ws?.addEventListener('error', e => {
        console.log(e)
        reject(e)
      })
    })

    this.ws?.addEventListener('message', e => {
      const data = JSON.parse(e.data)
      if (data.id) {
        const callback = this.callbacks[data.id]
        callback(data)
        delete this.callbacks[data.id]
      } else { // id onDownloadStart, onDownloadError
        console.log(data)
      }
    })
  }

  ready = () => this.readyPromise

  addUri               = (...args: any[]) => sendCall.call(this, 'aria2.addUri', ...args)
  addTorrent           = (...args: any[]) => sendCall.call(this, 'aria2.addTorrent', ...args)
  addMetalink          = (...args: any[]) => sendCall.call(this, 'aria2.addMetalink', ...args)
  getUris              = (...args: any[]) => sendCall.call(this, 'aria2.getUris', ...args)
  getFiles             = (...args: any[]) => sendCall.call(this, 'aria2.getFiles', ...args)
  getServers           = (...args: any[]) => sendCall.call(this, 'aria2.getServers', ...args)
  getPeers             = (...args: any[]) => sendCall.call(this, 'aria2.getPeers', ...args)
  getOption            = (...args: any[]) => sendCall.call(this, 'aria2.getOption', ...args)
  getGlobalOption      = (...args: any[]) => sendCall.call(this, 'aria2.getGlobalOption', ...args)
  getVersion           = (...args: any[]) => sendCall.call(this, 'aria2.getVersion', ...args)
  getSessionInfo       = (...args: any[]) => sendCall.call(this, 'aria2.getSessionInfo', ...args)
  getGlobalStat        = (...args: any[]) => sendCall.call(this, 'aria2.getGlobalStat', ...args)
  changePosition       = (...args: any[]) => sendCall.call(this, 'aria2.changePosition', ...args)
  changeUri            = (...args: any[]) => sendCall.call(this, 'aria2.changeUri', ...args)
  changeOption         = (...args: any[]) => sendCall.call(this, 'aria2.changeOption', ...args)
  changeGlobalOption   = (...args: any[]) => sendCall.call(this, 'aria2.changeGlobalOption', ...args)
  pause                = (...args: any[]) => sendCall.call(this, 'aria2.pause', ...args)
  pauseAll             = (...args: any[]) => sendCall.call(this, 'aria2.pauseAll', ...args)
  purgeDownloadResult  = (...args: any[]) => sendCall.call(this, 'aria2.purgeDownloadResult', ...args)
  unpause              = (...args: any[]) => sendCall.call(this, 'aria2.unpause', ...args)
  unpauseAll           = (...args: any[]) => sendCall.call(this, 'aria2.unpauseAll', ...args)
  remove               = (...args: any[]) => sendCall.call(this, 'aria2.remove', ...args)
  removeDownloadResult = (...args: any[]) => sendCall.call(this, 'aria2.removeDownloadResult', ...args)
  forcePause           = (...args: any[]) => sendCall.call(this, 'aria2.forcePause', ...args)
  forcePauseAll        = (...args: any[]) => sendCall.call(this, 'aria2.forcePauseAll', ...args)
  forceRemove          = (...args: any[]) => sendCall.call(this, 'aria2.forceRemove', ...args)
  forceShutdown        = (...args: any[]) => sendCall.call(this, 'aria2.forceShutdown', ...args)
  tellStatus           = (...args: any[]) => sendCall.call(this, 'aria2.tellStatus', ...args)
  tellActive           = (...args: any[]) => sendCall.call(this, 'aria2.tellActive', ...args)
  tellWaiting          = (...args: any[]) => sendCall.call(this, 'aria2.tellWaiting', ...args)
  tellStopped          = (...args: any[]) => sendCall.call(this, 'aria2.tellStopped', ...args)
  shutdown             = (...args: any[]) => sendCall.call(this, 'aria2.shutdown', ...args)
  saveSession          = (...args: any[]) => sendCall.call(this, 'aria2.saveSession', ...args)
  listMethods          = (...args: any[]) => sendCall.call(this, 'system.listMethods', ...args)
  listNotifications    = (...args: any[]) => sendCall.call(this, 'system.listNotifications', ...args)
  multicall            = (...args: any[]) => sendCall.call(this, 'system.multicall', ...args)
}

async function sendCall(this: Aria2Client, method: string, ...args: any[]) {
  await this.ready()
  return await new Promise((resolve, reject) => {
    const id = this.id++

    function callback(data: any) {
      if (data.error) reject(data.error)
      else resolve(data.result)
    }

    this.callbacks[id] = callback

    this.ws?.send(JSON.stringify({
      jsonrpc: '2.0',
      id: id,
      method: method,
      params: [`token:${this.secret}`, ...args]
    }))
  })
}
